* {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5 {
    font-family: 'Oswald', sans-serif;
}

h2 {
    margin-top: 0;
}

p {
    padding-bottom: 1rem;
    line-height: 28px;
}

a {
    color: black;
}

span {
    font-weight: bold;
}

li {
    text-align: left;
}

.col {
    li {
        line-height: 28px;
    }
}

body {
    font-family: 'Lato', sans-serif;
    font-size: 1.2rem;
    text-align: left;
}

nav .brand-logo {
    font-family: 'Oswald', sans-serif;
    font-size: 1.4rem;
}

.flashlight {
    transform: rotate(90deg);
}

.header {
    padding: 0.75rem;
}

.feature {
    padding-top: 20px;
    align-items: center;
    display: flex;
}

.map-img-container {
    width: 100%;
    img {
        width: 100%;
        height: 100%;
    }
}

.map-link {
    color: white !important;
}
